/**
 *
 * JsonBuilder
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Card, CardBody } from 'reactstrap';

import createFakeEvent from '../../cb-utils/createFakeEvent';
// @ts-ignore
import ReactJson from 'react-json-view';
import { CBEvent } from 'utils/types';

const CardWrapper = styled(Card)`
  & .jsonName {
    display: inline-block;
  }

  & input {
    min-width: 5rem;
  }
`;

interface IProps {
  value: object;
  onChange: (e: CBEvent<object>) => void;
}

interface EditValue {
  updated_src: object;
}

class JsonBuilder extends React.PureComponent<IProps, {}> {
  render() {
    return (
      <CardWrapper>
        <CardBody>
          <ReactJson
            src={this.props.value}
            name={null}
            onAdd={(val: EditValue) =>
              this.props.onChange(createFakeEvent(val.updated_src))
            }
            onEdit={(val: EditValue) =>
              this.props.onChange(createFakeEvent(val.updated_src))
            }
            onDelete={(val: EditValue) =>
              this.props.onChange(createFakeEvent(val.updated_src))
            }
          />
        </CardBody>
      </CardWrapper>
    );
  }
}

export default JsonBuilder;



